/***********************************************************************************************/
/* 01. GENERAL STYLING  */
/***********************************************************************************************/

body {
                                    
    font: 16px/28px @body-font; 
    color: @default-font-color;
    font-style: normal;
    font-weight: 300;
    position: relative;
}

img{
    max-width: 100%;
}

a {
    color: darken(@primary-color, 10%);
    text-decoration: none;   
    .transition(all, 0.3s);
}

a:hover,
a:focus {

    
    color: @primary-color;
    text-decoration: none;

}

blockquote{
    font: @body-font;
    color: @text-color;
}

h1, h2, h3, h4, h5, h6 {
    font-family: @heading-font;
    font-style: normal;
    font-weight: 600;
    color: @heading-font-color;
    line-height: 32px;
    margin: 0 0 16px 0;
}

h1{
    font-size: 36px;
}

h2{
    font-size: 24px;
}

h2+p{
    margin-top: 12px;
}

h3{
    font-size: 20px;
}

h3+p{
    margin-top: 12px;
}

h4{
    font-size: 18px;
}

h4+p{
    margin-top: 12px;
}

h5{
    font-size: 15px;
}

h6{
    font-size: 15px;
}

* + p {
    margin-top: 12px;
}

p {
    font: 16px/28px @body-font; 
    color: @default-font-color;
    font-style: normal;
    font-weight: 300;
    letter-spacing:0.01em;
    margin: 0 0 12px;
    padding: 0;
}

ul, li{

    list-style-type: none;
}

input[type=text],
input[type=password],
input[type=submit],
input[type=button],
textarea{

    .round(0);
    .drop-shadow(0, 0, 0, 0, 0);

    &:focus{

        .drop-shadow(0, 0, 0, 0, 0);

    }

