/*----- SECTION APPOINTMENT ----*/
 
.section-appointment-bg{
    background-image: url("../images/appointment_bg.jpg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    position: relative;
    overflow: hidden;
}


.appointment-form-wrapper{

    background: rgba(255,255,255,0.9);
    padding: 42px 0px 32px;
    border: 1px solid lighten( @light-border-color, 15%);
    .drop-shadow( 0px,  0px, 25px, 10px, .05 );
    .round(4px);

    .appointment-form-heading{
        position:relative; 
        background:@primary-color;
        padding: 30px;
        margin: -1px -1px 48px -1px;

        h3{
            position:relative; 
            clear: both;
            overflow: hidden;
            font-size: 24px;
            font-weight: 600;
            color:@light-text-color;
            letter-spacing:0.03em;
            margin: 0;
            display: inline;
            text-transform:uppercase;

            &:after{
                position: absolute;
                content: '';
                top: 50%;
                right: 0;
                height: 1px;
                width: 60px;
                margin-right: -64px;
                margin-top: 1px;
                background: @pure-white-bg;
            }
            
            
        }
        
        h3.no-heading-line{
            clear: both;
            &:after{
                display: none;
            }
        }

        span{
            float: right;
            display: inline;
            background: darken( @pure-white-bg, 2%);
            color: @dark-text-color;
            font-weight: 400;
            padding: 0px 16px;
            font-size: 16px;
            line-height: 32px;
        }
    }

    .appoinment-form{

        padding: 0 12px;

        .form-control{
            font-size:14px;
        } 

        .form-group{
            margin-bottom:0;
            position: relative;

            i{
                position: absolute;
                top: 12px;
                right: 32px;
                font-size: 17px;
                color: #c8c8c8;
                transition : all 0.5s ease 0s;
            }
        }
        
        .form-control::-moz-placeholder{
            color: lighten( @text-color, 25%);
            font-size: 14px;
            text-transform: uppercase;
        }
        

        input[type=text],
        input[type=email],
        input[type=url],
        input[type=password]{
            color: lighten( @text-color, 25%);
            border: 1px solid lighten( @primary-color, 30%);
            line-height: 42px;
            padding-right: 32px;
            height: 42px;
            margin-bottom: 24px;
            .round( 6px);
            .drop-shadow(0,0,0,0);
            .inner-shadow(0,0,0,0);
            .transition( 0.3s);
            
            &:focus{

                border: 1px solid @light-border-color;
                .drop-shadow(0,0,0,0);
                .inner-shadow(0,0,0,0);

            }

        }


        textarea{
            color: lighten( @text-color, 25%);
            border: 0px;
            border: 1px solid lighten( @primary-color, 30%);
            padding-left: 2px;
            margin-bottom: 12px;
            padding:6px 12px;
            .round( 6px);
            .drop-shadow(0,0,0,0);
            .inner-shadow(0,0,0,0);
            .transition( 0.3s);
            &:focus{

                .drop-shadow(0,0,0,0);
                .inner-shadow(0,0,0,0);
                border: 1px solid @light-border-color;

            }

        }

        select{
            border: 0px;
            border: 1px solid lighten( @primary-color, 30%);
            padding-left: 2px;
            margin-bottom: 24px;
            padding:6px 12px;
            .round( 6px);
            .drop-shadow(0,0,0,0);
            .inner-shadow(0,0,0,0);
            .transition( 0.3s);
            &:focus{
                .drop-shadow(0,0,0,0);
                .inner-shadow(0,0,0,0);
                border: 1px solid @light-border-color;

            }

        }

        .select-style{

            position:relative;       

            select.form-control{
                
                 border: 1px solid lighten( @primary-color, 30%);
                -webkit-appearance: none;
                -moz-appearance: none;
                position:relative;
                background-image:url("../images/down_arrow.png");
                background-position:100% 4px;
                background-repeat: no-repeat;
                cursor:pointer;
                box-shadow:none;
                color: lighten( @text-color, 25%);
                padding:6px 12px;
                height:42px;
                line-height:32px;
                margin-bottom:24px;
                font-size: 14px;
                .round( 6px);
            }
        }

    }


}


