    .gallery-container{
        padding: 0px;
        margin: 0;

    }
}

.gallery-light-box{

    display: block;
    overflow: hidden;
    position: relative;

    .gallery-img{

        display: block;
        position: relative;
        max-width: 100%;
        padding: 0px;

        &:before{
            background: rgba(0, 0, 0, 0.3);
            width: 100%;
            height: 0px;
            position: absolute;
            top: 0;
            left: 0;
            content: '';
            .transition(all, 0.5s);
        }

        &:after{
            font-family: FontAwesome;
            content: '\f1c5';
            position: absolute;
            right: 150%;
            margin-left: -21px;
            bottom: -64px;
            background: @light-text-color;
            color: @primary-color;
            text-align: center;
            width: 42px;
            height: 42px;
            line-height: 42px;
            .round(6px);
            .transition(all, 0.9s);

        }

        img{
            width: 100%;
            max-width: 100%;
            display: block;
            opacity: 1;
            .transition(all, 0.7s);
        }

    }

    &:hover{


        .gallery-img{

            &:before{
                background: rgba(0, 0, 0, 0.7);
                width: 100%;
                height: 1500px;
            }

            &:after{
                opacity: 1;
                top: 50%;
                left: 50%;
                margin-top: -24px;
                .round(100%);
            }

            img{

                opacity: 0.4;
            }

            figcaption{
                bottom: 0px;
                left: 0;
            }

        }



    }




}

.filterable-gallery{

    clear: both;

    .filter-items{
        text-align: center;
        margin-bottom: 32px;

        button{

            text-transform: uppercase;
            .round(0);
            margin-right:4px;
            border: 1px solid @light-border-color;

            &.active{
                background: @primary-color;
                color: @light-text-color;
            }

            &:last-child{
                margin-right: 0px;
            }

            &:hover,
            &:foucs{
                background: @primary-color;
                color: @light-text-color;
            }
        }
    }

}

.gallery-carousel{

    clear: both;
    
    [class^="col-"] {
        width: 100%;
    }

    .owl-nav {
        clear: both;
        color: #000000;
        line-height: 32px;
        height: 32px;
        margin-top: -14px;
        opacity: 0.9;
        position: absolute;
        top: 50%;
        width: 100%;
        z-index: 1;
        -webkit-transition: all 0.3s ease-in-out ;
        -moz-transition: all 0.3s ease-in-out ;
        transition: all 0.3s ease-in-out ;

        div.owl-prev {
            background: @primary-color;
            text-align: center;
            .transition(all, 0.5s);
            width:32px;
            height: 32px;
            line-height: 32px;
            font-size: 18px;
            color: darken( @light-bg, 5%);
            float: left;
            .round(0px);

            &:hover{
                background: @dark-bg;
            }
        }

        div.owl-next {

            background: @primary-color;
            text-align: center;
            .transition(all, 0.5s);
            width:32px;
            height: 32px;
            line-height: 32px;
            font-size: 18px;
            color: darken( @light-bg, 5%);
            float: right;
            .round(0px);

            &:hover{
                background: @dark-bg;
            }
        }


        &:hover{
            opacity: 0.8;
        }



    }

    .owl-dots{

        margin: 12px auto 0 auto;
        width: 100px;
        text-align: center;

        .owl-dot{
            .round(4px);
            display: inline-block;
            padding: 2px;
            width: 12px;
            height: 8px;
            background: darken( @light-bg,20%);
            margin: 0 2px;

            span{
                display: none;
            }
        }

        .active{
            .round(4px);
            width: 16px;
            background: @primary-color;
            span{
                display: none;
            }

        }

    }

    &:hover{

        .owl-nav{
            opacity: 1;
        }

    }

}  

