                li{
                    a{
                        color: @light-text-color;
                    }
                }


            }
        }

    }

    .logo{
        text-align: center;
    }

    .section-heading-wrapper,
    .section-heading-wrapper-alt{
        margin-bottom: 0px;
        
        h2{
            font-size: 32px;
            letter-spacing: 0em;
            line-height: 42px;
        }

        h3{
            display: none;
        }
        
    }

    .section-content-block{
        padding: 32px 0;
    }

    // Page Header.

    .page-header{
        padding: 80px 0;        
        h3{
            font-size: 24px;
            line-height: 32px;
            margin: 12px 0;
        }
    }    

    /*----- HOME SLIDER ----*/

    #slider_1{

        .slider_item_container{
            min-height: 300px;
        }

        .slider-content {
            top: 50%;
            transform: translateY(-55%);  
            
            i.icon{
                &:after,
                    &:before{
                    width:50px;
                    line-height:50px;
                    height:50px;
                    font-size:20px;
                    margin:40px 0 10px;
                }

            }


            h2 {

                font-size: 42px;
                line-height: 48px;
                padding-bottom:0px;
                margin-bottom:12px;

            }

            h3 {
                display: none;
            }

            .slider-button{
                margin-top: 10px;  

                a.btn-slider {
                    font-size: 14px;
                    display:inline-block;
                }

            }

        }


    }
    
    /*-----  ABOUT ----*/
    
    .about-img{
        margin: 24px 0;
    }
    
    .about-highlight{
        background: @light-bg;

        .highlight-box{
            border:1px solid @border-color;

            h3{
                font-size: 18px;
            }

        }

    }

    /*----- PROCESS BLOCK  ----*/
    
    .process-step-1,
    .process-step-2{
        margin-bottom: 24px;
    }

    /*----- CTA ----*/
    
    .section-cta-bg-1{
      background: #f1f5f8;  
    }

    .cta-layout-01,
    .cta-layout-02,
    .cta-video-box{
        
        h2{
        
             font-size: 42px;
             line-height: 60px;
            
            span{
             
                font-size: 42px;
                line-height: 60px;
                
            }
            
        }
        
        h4{
            font-size: 24px;
            line-height: 32px;
        }
    }
    

    /*----- APPOINTMENT FORM ----*/
    
    .appointment-form-wrapper{
        
        clear: both;
        margin: 12px 0 48px 0;

        .appointment-form-heading{
            position:relative; 
            background:@primary-color;
            padding: 12px 24px 18px;
            margin: -1px -1px 48px -1px;
            text-align: center;

            h3{
                margin: 0;
                display: block;

                &:after{
                    display: none;
                }
            }

            span{
                float: none;
                display: block;
                margin-top: 12px;
            }
        }

        .appoinment-form{

            padding: 32px 6px;

        }


    }

    /*----- TEAM LAYOUT  ----*/
    
    .team-layout-1{
        margin-bottom: 24px;
    }

    /*----- GALLERY LAYOUT  ----*/

    .filterable-gallery{

        clear: both;

        .filter-items{
            text-align: center;
            margin-bottom: 24px;

            button{
                margin-bottom: 6px;
            }
        }

    }
    
    /*----- NEWSLETTER LAYOUT  ----*/

    .horizontal-newsletter{
        .news-subscription {
            button {
                font-size: 16px;
                width: 100px;
            }
        }
    }

    /*----- BLOG LAYOUT  ----*/

    .latest-news-container{

        border: 0px solid @light-border-color;

        .news-content{

            padding: 12px 16px 24px;

        }

    }

    /*----- BANNER ----*/

    .section-banner{
        
        padding:80px 0px;  

        .banner-content{
            transform: translateY(-20px);  

            h2{
                font-size: 32px;
            }

            h3{
                display: none;
            }

            a.btn-slider{
                margin-top: 16px;
                font-size: 14px;
                font-weight: 500;
            }

        } 

    }
   
    /*----- FAQ LAYOUT ----*/
    
    .faq-layout .faq-box .panel-heading [data-toggle="collapse"]::after{
        display:none;  
    }
    
    /*-----TESTIMONIALS ----*/
    
    .testimony-layout-1{
        max-width: 100%;
    }
    
    /*----- COUNTDOWN LAYOUT ----*/

    #coming_countdown{

        font-size: 24px;

        .countdown_container{
            span.count_number{
                font-size: 50px;
                line-height: 40px;
            }
            span.count_text{
                font-size: 14px;
                line-height: 42px;
            }

        }

    }

    /*----- LOGO LAYOUT ----*/

    .logo-layout-1,
    .logo-layout-2{

        .logo{

            margin-top: 0px;

        }

    }


    // 404 PAGE.

    .message-container-404{

        margin: 48px 0;

        .text-404{
            margin:80px 0;
        }

        .search-form-404 {
            padding: 5px;    
            width: 100%;
        }

        .message-text-404{
            padding: 24px 0px;
        }

    }

    // Footer.

    .about-footer{
        margin-bottom: 32px;
        padding-bottom: 22px;
    }

    .footer-widget-area {

        padding: 0;

        .textwidget {

            i.fa{

            }

            p {


            }
        }

    }

    .footer-widget{
        margin-bottom: 36px;

        .footer-useful-links{
            clear: both;

            li{
                width: 100%;
                float: none;
            }
        }
    }

    .footer-intro{
        display: none;
    }

    .footer-nav{
        display: none;
    }

    .copyright-text{
        text-align: center;
    }

}

/* Tab Portrait Size to Tab Landscape Size (devices and browsers) */
@media only screen and (min-width:380px) and (max-width: 480px)  {
    .xs-margin{
        margin-left: 20px;
        margin-right: 20px;
    }
}

@media only screen and (min-width:480px) and (max-width: 600px)  {
    .xs-margin{
        margin-left: 70px;
        margin-right: 70px;
    }
}

@media only screen and (min-width:600px) and (max-width: 767px)  {
    .xs-margin{
        margin-left: 120px;
        margin-right: 120px;
    }
    
}